 .form-container {
    /* margin: 0 auto; */
    display: flex;
    align-items: center;
    justify-content: center;
    /* width: 1000px; */
    position: relative;
    height: 500px;
    /* display: grid; */
    /* grid-template-columns: 1fr 1fr; */
  }
  
  .form {
    /* position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%); */
    width: 1000px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  
  .form h1 {
    font-size: 1rem;
    text-align: start;
    width: 60%;
    color: #2F184B;
  }
  
  .form-container-inputs {
    margin-bottom: 0.4rem;
    width: 100%;
    display: flex;
    justify-content: center;
  }
  
  .form-container-inputs p, .form-checkbox p {
    font-size: 0.8rem;
    color: #f00e0e;
  }
  
  .form-label {
    display: inline-block;
    font-size: 0.8rem;
    margin-bottom: 6px;
    color: #532B88;
  }
  
  .form-input {
    padding-left: 10px;
    outline: none;
    border-radius: 10px;
    height: 50px;
    width: 58%;
    border: 1px solid black;
    font-size:16px;
  }
  
  .form-input::placeholder {
    color: grey;
    font-size: 14px;
    padding-left: 5px;
  }
  
  .form-button {
    width: 60%;
    height: 40px;
    margin-top: 40px;
    border-radius: 10px;
    background-color: #532B88;
    outline: none;
    border: none;
    color: #fff;
    font-size: 1rem;
  }
  
  .form-button:hover {
    cursor: pointer;
    background-color: #2F184B;
    transition: all 0.2s ease-out;
  }
  
  .form-already-login {
    font-size: 0.9rem;
    color: #2F184B;
    width: 80%;
    text-align: center;
    text-decoration: none;
    font-weight: 600;
  }

  .form-checkbox-label
  {
    display:inline-block;
    padding-top:10px;
  }

  .form-checkbox-label span
  {
    font-size: 0.8rem;
    color: #2F184B;
    font-weight:600;
  }

  .form-checkbox-input
  {
    width:15px;
    height:15px;
  }

  

